.UserTitle {
  display: flex;
}
.UserTitle .u-title-left {
  flex: 1 1;
}
.UserTitle .u-title-right {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.UserTitle .u-title-right span {
  font-size: 48px;
  color: #87d068;
}
.UserTitle .u-userName {
  font-size: 24px;
  font-weight: 300;
  margin: 10px 0 20px 0;
}
.u-content .userDetail {
  display: flex;
}
.u-content .userDetail .u-detail-left {
  flex: 1 1;
}
.u-content .userDetail .u-detail-left .u-tips-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.u-content .userDetail .u-detail-left .u-tips-content .user-tips {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 1px solid #eee;
  border-radius: 4px;
  font-size: 24px;
  font-weight: 600;
  height: 90px;
  width: 24%;
}
.u-content .userDetail .u-detail-left .u-tips-content .user-tips .u-tips-title {
  font-size: 12px;
  margin-bottom: 10px;
  font-weight: normal;
}
.u-content .u-log-content {
  margin-top: 30px;
}
.u-content .u-log-content .ant-timeline-item-content {
  padding-top: 30px;
}
.u-content .u-log-content .time-line-content {
  display: flex;
}
.u-content .u-log-content .time-line-content .right {
  top: -30px;
  border: 1px solid #333;
  border-radius: 7px;
  /* position: relative; */
  margin-top: -29px;
  margin-left: 16px;
  /* width: 44%; */
  float: left;
  border: 1px solid #d4d4d4;
  border-radius: 18px;
  padding: 20px;
  position: relative;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
}
.u-content .u-log-content .time-line-content .right::before {
  position: absolute;
  top: 26px;
  right: -15px;
  display: inline-block;
  border-top: 15px solid transparent;
  border-left: 15px solid #ccc;
  border-right: 0 solid #ccc;
  border-bottom: 15px solid transparent;
  content: " ";
  border-left-width: 0;
  border-right-width: 15px;
  left: -15px;
  right: auto;
}
.u-content .u-log-content .time-line-content .right::after {
  position: absolute;
  top: 27px;
  right: -14px;
  display: inline-block;
  border-top: 14px solid transparent;
  border-left: 14px solid #fff;
  border-right: 0 solid #fff;
  border-bottom: 14px solid transparent;
  content: " ";
  border-left-width: 0;
  border-right-width: 14px;
  left: -14px;
  right: auto;
}
